@charset "UTF-8";
/*         Загружаемые модули         */
/*         Основные размеры        */
/*         Основные цвета        */
html {
  height: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  height: 100%;
  min-width: 320px;
  max-width: 100%;
  margin: 0 auto;
  margin-top: auto;
  padding: 0;
  font-family: "Circe", "Arial", sans-serif;
  font-size: 16px;
  line-height: 150%;
  font-weight: normal;
  color: #000000;
  overflow-x: hidden;
  background-color: #ffffff; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* "Circe" font-family */
@font-face {
  font-family: "Circe";
  font-style: normal;
  font-weight: 300;
  src: local("Circe"), url("../fonts/circe/circe_300.eot") format("embedded-opentype"), url("../fonts/circe/circe_300.woff2") format("woff2"), url("../fonts/circe/circe_300.woff") format("woff"), url("../fonts/circe/circe_300.svg") format("svg"); }

@font-face {
  font-family: "Circe";
  font-style: normal;
